.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.center {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.td-notSelected{
  width: 40px;
  height: 40px;
  border-color: white;
  border: 1px solid;
  cursor: pointer;
}

.td-selected{
  width: 40px;
  height: 40px;
  border-color: white;
  border: 1px solid;
  background-color: grey;
}

.td-found{
  width: 40px;
  height: 40px;
  border-color: white;
  border: 1px solid;
  background-color: green;
}

.td-foundNotRight{
  width: 40px;
  height: 40px;
  border-color: white;
  border: 1px solid;
  background-color: rgb(189 191 59);
}

table {
  border-color: white;
  border: 1px solid;
}